.cube-wrapper {
    width: 550px;
    height: 250px;

    box-shadow:3px 5px 50px 16px #b3b3b3;
    -webkit-box-shadow:3px 5px 50px 16px #b3b3b3;
    background: aqua;
    transform: translate(-10%, -8%);
    -webkit-transform: translate(-10%, -8%);
}


.cubo, .cubo div {
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  position: absolute;
  transform-style: preserve-3d;
  -webkit-transform-style: preserve-3d;
  transition: transform .5s ease-in-out;
  -webkit-transition: transform .5s ease-in-out;
}

.frente {
  background: white;
  padding-left: 24%;
  font-size: 50px;
  padding-top: 68px;
  font-family: monospace;
}



.parte-cima {
    background: tomato;
    transform: translate(0, -100%) rotateX(90deg);
    -webkit-transform: translate(0, -100%) rotateX(90deg);
    -webkit-transform-origin: bottom;
    transform-origin: bottom;
    padding-left: 24%;
    font-size: 50px;
    padding-top: 68px;
    font-family: monospace;
}


.cube-wrapper:hover .cubo {
    transform: translate(0, 50%) rotateX(-90deg);
    -webkit-transform: translate(0, 50%) rotateX(-90deg);
}
